/*variables*/
:root {
  --theme-color:  #ff6600;
  --secondary-color: #828282;
}

body {
  font-family: Verdana, Geneva, sans-serif;
  font-size: 14px;
  margin: 0;
}

/*reset styles*/
p {
  margin: 0;
}

a {
  text-decoration: none;
  color: inherit;
}

.app{
  display: flex;
  justify-content: center;
}

.app-body{
  width: 85%;
  margin: 10px 0 20px;
}

@media (max-width: 480px) {
  .app-body{
    width: 100%;
    margin: 0;
  }
}

.app-header {
  padding: 2px 8px 2px 2px;
  background-color: var(--theme-color);
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.app-header nav{
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.app-header .app-logo{
  height: 18px;
  width: 18px;
  line-height: 15px;
  color: #ffffff;
  text-align: center;
  border: 1px solid #ffffff;
}

.app-header .app-title{
  margin: 0;
  margin-right: 15px;
  font-size: inherit;
}

.app-header .app-header-content{
  display: flex;
  flex-wrap: wrap;
  align-items: baseline;
  margin-left: 8px;
}

.app-header .header-aside{
  font-size: 12px
}

.app-header .header-nav,
.app-content-footer .footer-links {
  font-size: 12px;
  margin-bottom: 4px;
}

.app-header .header-nav a:first-child{
  padding-left: 0;
}

.app-content {
  padding: 8px 0;
  background-color: #f6f6ef;
}

.news-item {
  display: grid;
  grid-template-rows: 100% auto;
  grid-template-columns: 35px auto;
  column-gap: 4px;
  margin: 4px 0;
}

.news-item .news-item-index {
  display: flex;
  align-items: flex-start;
  justify-content: flex-end;
  color: var(--secondary-color);
  text-align: right;
}

.news-item .news-item-caret {
  display: inline-block;
  border-bottom: 7px solid var(--secondary-color);
  border-top: 7px solid transparent;
  border-left: 4px solid transparent;
  border-right: 4px solid transparent;
  margin-top: -2px;
  margin-left: 2px;
}

.news-item .news-item-body {
  grid-column-start: 2;
}

.news-item .news-item-headline {
  display: inline;
}

.news-item .news-item-ref {
  font-size: 10px;
  color: var(--secondary-color);
}

.news-item .news-item-details {
  font-size: 10px;
  color: var(--secondary-color);
}

.news-item .news-item-more{
  margin: 8px 0;
}

.app-content-footer {
  text-align: center;
  border-top: 2px solid var(--theme-color);
}

.app-content-footer .footer-links{
  margin-top: 15px;
}

.app-content-footer .footer-search{
  margin: 15px 0;
  color: var(--secondary-color);
}

.app-content-footer .footer-search input{
  height: 20px;
}
